// @name Header
// @description All header styles

header {
    padding: 3em 0 1.5em;
    margin-bottom: 1.5em;
    @media(min-width: 640px) {
	    padding-top: 4.5em;
	}
    
}

.intro {
    margin-bottom: $spacer;

    h1 {
	    font-family: 'Droid Serif', serif;
	    font-weight: 300;
    }
    
    em {
	    font-weight: 700;
    }
}

.social {
    a {
        margin-right: $spacer;
    }
}



header {
	width: 100vw;
	min-height: 100vh;
	padding: 0;
	display: flex;
}

.intro {
	max-width: $content-max-width;
	margin: auto;
	padding: 2rem;
}