// @name Resume
// @description All resume styles

.section-title {
    text-transform: uppercase;
    letter-spacing: 2px;
    position: relative;
    margin-bottom: 2em;

    &:after {
        content: '';
        position: absolute;
        bottom: -0.5em;
        left: 0;
        //margin-left: -25px;
        width: 50px;
        height: 1px;
        background: rgb(0,170,0);
    }
}

dd,
dt {
	p {
		margin-bottom: 0;
	}
}
dt {
	padding-left: 1em;	
	margin-bottom: 1em;
	border-left: 2px solid $grey-light;
}
dd {
	margin-bottom: 2em;
}


.study {
	font-weight: 700;
}
.title,
.degree {
    font-weight: 700;
}

