@import
    'reset',
    'variables',
    'layout',
    'typography',
    'header',
    'footer',
    'resume'
;


body {
	background: #fafafa;
}
a {
    color: rgb(0,170,0);
    //background: rgba(5,171,75, 0.1);
    transition: all ease-out .2s;
    border-bottom: 2px solid rgba(0,170,0, 0.7);
	text-decoration: none;

    &:hover {
        background: rgba(5,171,75, 0.1);
        text-decoration: none;
        border-bottom-color: rgb(0,170,0);
    }
}

