// @name Typography
// @description All typography settings and styles

//@import 'https://fonts.googleapis.com/css?family=Merriweather:300,700';

body {
	font-family: 'Georgia', serif;
	//font-weight: 300;
	font-style: normal;
	color: #222;

}
header {
	font-size: 1.5em;
	line-height: 1.5;
}

.page-content {
	font-size: 1.125em;
	line-height: 1.5;
}

h1 {
	font-family: sans-serif;
	font-weight: 300;
	font-size: 2.1666666666666665em;
	line-height: 1.3846153846153848;
	margin-top: 0.6923076923076924em;
	margin-bottom: 0.75em;
}
h2 {
	font-family: sans-serif;
	font-weight: 300;
	font-size: 1.6666666666666667em;
	line-height: 1.7999999999999998;
	margin-top: 0.8999999999999999em;
	margin-bottom: 0.8999999999999999em;
}
h3 {
	font-family: sans-serif;
	font-weight: 300;
	font-size: 1.2777777777777777em;
	line-height: 1.173913043478261em;
	margin-top: 1.173913043478261em;
	margin-bottom: 1.1em;
}
h4 {
	font-weight: 300;
	font-size: 1em;
	line-height: 1.5;
	margin-top: 1.5em;
	margin-bottom: 0em;
}
h5 {
	font-weight: 300;
	font-style: italic;
	font-size: 1em;
	line-height: 1.5;
	margin-top: 1.5em;
	margin-bottom: 0em;
}
p,
ul,
ol,
pre,
table,
blockquote {
	margin-top: 0em;
	margin-bottom: 1.5em;
}
ul ul,
ol ol,
ul ol,
ol ul {
	margin-top: 0;
	margin-bottom: 0;
}
ol {
	padding-left: 2em;
	list-style: decimal;
}
hr {
	border: 1px solid;
	margin: -1px 0;
}
b,
strong,
em,
small,
code {
	line-height: 1;
}
strong, 
em {
	font-weight: 700;
}
sup {
	line-height: 0;
	position: relative;
	vertical-align: baseline;
	top: -0.5em;
}
sub {
	bottom: -0.25em;
}